<template>
  <div class="card">
    <div class="card-header">
        Exam08View
    </div>
    <div class="card-body">
        <div>
            kind:{{route.params.kind}}
        </div>
        <div>
            color:{{route.params.color}}
        </div>
        <hr/>
        <div>
            kind:{{route.query.kind}}
        </div>
        <div>
            color:{{route.query.color}}
        </div>
    </div>
  </div>
</template>

<script setup>
    //Route : 이동 URL 의 정보를 가지고 있는 객체[전달된 데이터가 뭔지 알고 싶을 떄]
    //Router : 이동 시켜주는 주체[전달하는 자체가 무엇인지 알려고 할때]
    import {useRoute} from "vue-router";
    import {toRaw} from "vue";

    const route = useRoute();
    console.log(route);
    console.log("---------------------------------------------------------");
    console.log(toRaw(route));
    console.log("---------------------------------------------------------");
    console.log(JSON.parse(JSON.stringify(route)));
    console.log("---------------------------------------------------------");
    console.log("kind : " + route.params.kind);
    console.log("color : " + route.params.color);
     console.log("---------------------------------------------------------");
    console.log("kind : " + route.query.kind);
    console.log("color : " + route.query.color);
</script>

